var openNew = function(url){
	
	var win = window.open(url, '_blank');
	win.focus();
}
var openZerodha = function(){
	
	   openNew('https://zerodha.com');
   }
   var openNSE = function(){
	
	   openNew('https://www.nseindia.com/');
   }

var d = function(content,text) {
	
	var result = [];var str = '';
	var codesArr = JSON.parse(content);
	var passLen = text.length ;
	
	for(var i = 0  ; i < codesArr.length ; i++) {
		var passOffset = i%passLen ;
		var calAscii = (codesArr[i]-text.charCodeAt(passOffset));
		result.push(calAscii) ;
	}
	for(var i = 0 ; i < result.length ; i++) {
		var ch = String.fromCharCode(result[i]); str += ch ;
	}
	return str ;
}
var terms = function(){
	let url = window.location.origin +'/html/terms.html';

	var win = window.open(url, '_blank');
	win.focus();
}
var sendEmail= async function( data ){
	let msg = null;
	let responsejson = null;
	let url = 'https://api.sendinblue.com/v3/smtp/email';
	let content = d('[197,177,182,176,234,181,146,148,124,148,139,106,213,107,199,164,126,119,133,107,170,126,102,152,170,152,145,104,213,109,150,164,179,119,183,111,218,124,97,205,169,199,142,104,165,155,149,166,125,169,137,153,173,128,145,155,121,154,191,147,170,104,155,119,125,121,182,111,216,130,149,205,113,176,159,130,169,172,176,115,180,138,180,144,168,190,101,203,135]','MFQ7wL0gDcY1r5dC');
	let options  = null;
//	console.log(n(key,'MFQ7wL0gDcY1r5dC'));
	try{
		
	
		options = { 
				
			method : 'post',
			body : JSON.stringify(data),						
			headers: {					
				'Content-Type': 'application/json ; charset=UTF-8',
				'accept': 'application/json',					 
				'api-key':content,
			}
		};

		let response = await fetch( url,options )
		if( response.ok ){
			let json = await response.json()
			.then( function( data ){				
				responsejson = data;				
			})
		}	
		else{			
			msg = 'Message from server NOT OK ' + response.status;			
		}
	}	
	catch( err ) {
		
		msg = 'Error while communicating with server => ' + err.message;		
		responsejson = {code:-100, message:msg };			
	}
		
	return responsejson;
	
}

export {
	sendEmail,
	openNew,
	terms,
	d,
	openZerodha, 
	openNSE,
}


