import React, { Component } from 'react';

export default class Description extends Component{

	render(){
		
		return(
		<div >
			<div class=' w3-panel   w3-left-align'  >				
				<p  style={{ lineHeight: 1.8,fontSize:'15px'}}>
					Prameyaka is the cloud-based trading platform designed to provide an ultra-simple day trading solution for those who don't have trading skills, stock market knowledge or extra time to spend. Packed with pre-build strategies and algorithms, this platform consolidates analytical skills with business expert's expertise to provide complete day-trading solution.
				</p>	
			</div>
			</div>
		)
	}
}


