import React, { Component } from 'react';
import 'w3-css/w3.css';
import faqbg from './../images/15.jpg';
const faqs = {
	'1':{
		q : 'Is it safe to login with credentials provided by the broker?',
		a : 'Your broker has security features that will never disclose your credentials to any third-party applications. When you open Prameyaka application, you will be redirected to your broker\'s authentication, where you provide your credentials. Once authenticated, It will be redirected back to us with a token which is valid ONLY for a day. Once you authorize our application, we get the access to manage your portfolio. Our application can not do any money transactions like withdrawal, but has read-only access to the funds available in your trading account, which is required for sizing your positions.'
		
	},
	'2':{
		q : 'Can we set the specific amount to be utilized by the platform?',
		a : 'YES, you can allocate the amount which Prameyaka needs consider for sizing your positions . At any point of time, your total margin utilized by the platform will be less than the amount you set. By default, Prameyaka will consider all your available funds if less than maximum amount set by the platform.',

	},
	'3' :{
		q : 'Why subscription charges are so high?',
		a: "Prameyaka subscription charges seem to be high when you get a good return for the investment. We like customers who don't care about charges when they are earning good profits. At the same time, we understand their concern if we charge even when they are not making a decent profit. You should note that our charges are not for automating your trades, not based on a total number of recommendations or not based on the total amount of transactions. Our charges are not for the profit earned by individual scripts or not even for the daily return, but we charge for the net monthly profit gained from the trades triggered by our platform.",
	}
}

export default class Faq extends Component{
	
	constructor( props ){
		
		super(props );
		this.state = {
			item : '0',
		};
	}


	render(){
		
		let cls = 'w3-left-align';
		let selected = false;
		let padding = this.props.device == 'L' ? '50px' : '1px';
		return (
		<div className='w3-container  w3-border' style={{maxWidth: '100%', paddingTop:'50px', paddingLeft:padding, paddingRight:padding, minHeight:'900px',fontFamily: 'Helvetica, Arial, sans-serif', fontSize:'17px', textShadow: '1px 1px 1px  #fff',  backgroundImage: "url(" + faqbg + ")", backgroundSize: 'cover', backgroundOrigin: 'border-box',backgroundPosition: 'top' , paddingTop:'35px'}}>
			<header className="w3-container w3-center">
				<h3 style={{ textTransform: 'uppercase', padding:'20px'}} >Frequenty asked questions</h3>
			</header>
			<div class = 'w3-border w3-animate-top ' >
				
				
			
				{
					Object.entries(faqs).map(([key,faq])=>{
						
						if(this.state.item == key )
							selected= true;						
						else selected= false;
						
						return(	
							<div class='w3-padding-small w3-border w3-padding-small'>				
								<a href='#' 
									id={key} 
									style={{textDecoration: "none" }}
									onClick={this.onQuestion.bind(this)}
									class=" w3-block  w3-left-align w3-padding-small " >
									<span className='w3-large'>Q.</span> {faq.q}
								</a>					 
								<p class={selected? 'w3-left-align w3-margin-left w3-text-dark-gray' : 'w3-left-align w3-hide'}>
									{faq.a}
									</p>
							</div>				
						
						)
					})					
				}
						
			</div>
			
			</div>
		);
		
	}
	
	onQuestion= function( event ){
		let source = event.target || event.source ;
		this.setState({item : source.id});
	}
	answer = function( no ){
		
	}
}


