import React, { Component } from 'react';

import logo from './../images/logo_m.png';
import logo_bw from './../images/logo_bw.png';
import { text, locale } from './../util/i18n.js';



const vars = {

	lfsize :'22px',
	mfsize :'20px',
	sfsize :'14px',
}
var c= 0;


export default class RollingSentences extends Component{

	constructor(props){
		super(props);


	}
	componentDidMount(){
		this.timer = setInterval( this.role.bind(this), 3000 );
	}
	componentWillUnmount(){
		clearInterval( this.timer );
	}


	render(){

		this.loadText();
		let cFontSize = this.props.device =='S'  ? vars.sfsize : (this.props.device =='M'|| locale() == 'ka' ? vars.mfsize : vars.lfsize );
		let centered = this.props.device =='S' || this.props.device =='M' ? ' w3-center ' : '' ;

		return (
			<div className ={'w3-round  w3-text-brown'+centered}
				style = {{ whiteSpace: 'nowrap',position: 'relative', textAlign:'left',  fontSize:cFontSize ,textShadow1: '1px 1px 0px #000', fontFamily:'Garamond', fontStyle: 'italic' }}>

					<span >{text("rollers-static")}<br/>{text("rollers-you")}
					<span id="first-word" >	just </span>
					<span >{text("rollers-need")}</span>&nbsp;
					<span id="second-word"><i>Prameyaka</i></span>
					</span>

			</div>
		);
	}
	role = function(){

		document.getElementById( "first-word" ).innerHTML =  "<span class='w3-animate-right ' >" + this.fword[c] + "</span>";
		document.getElementById( "second-word" ).innerHTML = "<span class='w3-animate-right' >" + this.sword[c] + "</span>";


			if( ++c > 7 ) c = 0;
	}
	timer = null;
	fword =[];
	sword =[];

	loadText = function(){
		this.fword =[];
		this.sword =[];
		this.fword = text("rollers-fword");
		this.sword = text("rollers-sword");
		/*[text("rollers-var11") ,
				text("rollers-var12") ,
				text("rollers-var13") ,
				text("rollers-var14") ,
				text("rollers-var15") ,
				text("rollers-var16") ,
				text("rollers-var17") ,
				text("rollers-var18")];

	 this.sword = [
				text("rollers-var21") ,
				text("rollers-var22") ,
				text("rollers-var23") ,
				text("rollers-var24") ,
				text("rollers-var25") ,
				text("rollers-var26") ,
				text("rollers-var27") ,
				text("rollers-var28") ];	*/
	}
}



