
import React, { Component } from 'react';
import logo_bw from './../images/logo_bw.png';
import about from './../images/about.jpg';

import { text,locale } from './../util/i18n.js';

export default class About extends Component{
	
	constructor( props )
   {
       super(props);
	   this.state ={ pagenm : 0 };
	   
   }
   
	render(){
		   
		let padding = this.props.device == 'L' ? '30px' : '2px';
		let width = this.props.device == 'L' ? '70%' : '100%';
		
		return (
		
			<div  style={{ paddingTop:padding,fontFamily: 'Helvetica, Arial, sans-serif', fontSize:'17px', textShadow: '1px 1px 1px  #fff', maxWidth: '100%',  backgroundImage: "url(" + about + ")", backgroundSize: 'cover', backgroundOrigin: 'border-box',backgroundPosition: 'top' , paddingTop:'35px'}}>
				
				<div className="w3-border w3-border-brown	 w3-center" style={{verticalAlign:'top', width:width, padding:padding, }} >
					<header className="w3-container w3-margin w3-center">
					  <h3>{text('about-heading')}</h3>
					</header>
					<p className='w3-left-align w3-margin'>

					{text('about-desc')[0]}</p>
					<p  className='w3-left-align w3-margin'>
						{text('about-desc')[1]}
					</p>
					<div  className='w3-left-align w3-padding'>
					{/*<h5>{text('about-goals')}</h5>  */}
						<p className='w3-padding animate__animated animate__fadeInUp '>
							<li >
								<b>{text('about-simple')[0]}&nbsp;:&nbsp;</b> {text('about-simple')[1]}
							</li>
						</p> 
						<p className='w3-padding animate__animated animate__fadeInUp animate__delay-1s'>
							<li >
								<b>{text('about-pricing')[0]}&nbsp;:&nbsp;</b>{text('about-pricing')[1]}
							</li>
							
							<span className='w3-small w3-opacity w3-hide'><i>We are planning to make a pricing flat 25% of customer's gain irrespective of loss or gain. In case of loss, 25% of their loss will be added to their account as Prameyaka Compensation Points (PCP) which can be used only for renewal charge of subsequent months. But we are still working on its feasibility and possible legal complexities with such a pricing structure.</i></span>
						</p>
						<p className='w3-padding animate__animated animate__fadeInUp animate__delay-2s'>
							<li className=''>
								<b>{text("about-hasslefree")[0]}&nbsp;:&nbsp;</b>{text("about-hasslefree")[1]}
							</li>
						</p>
						<p className=' w3-padding animate__animated animate__fadeInUp animate__delay-3s'>
							<li className=''>
								<b>{text("about-nomarketing")[0]}&nbsp;:&nbsp;</b>{text("about-nomarketing")[1]}
							</li>
						</p>
						<p className=' w3-padding animate__animated animate__fadeInUp animate__delay-4s'>
							<li className=''>
								<b>{text("about-transparency")[0]}&nbsp;:&nbsp;</b>{text("about-transparency")[1]}
							</li>
						</p>
						
					</div>
				
				
				</div>	
			</div>);
   }
}


