import React, { Component } from 'react';
import Website from './content/website.js';
import './App.css';
import 'w3-css/w3.css';

class App extends Component {
	
	
	
	constructor(props) {

    super(props);

	
	let dim = this.updateDimensions();
    this.state = {

      windowWidth: dim.windowWidth,
      windowHeight: dim.windowHeight,	
	  breakeven:500,
	  screenType:dim.screenType,
    };
	//this.state = this.updateDimensions;
   
  }

  componentDidMount() {

   // this.updateDimensions();
    window.addEventListener("resize", this.updateDimensions);
  }

  componentWillUnmount() {

    window.removeEventListener("resize", this.updateDimensions);
  }

  updateDimensions() {

    let windowWidth = typeof window !== "undefined" ? window.innerWidth : 0;
    let windowHeight = typeof window !== "undefined" ? window.innerHeight : 0;
	
	let type = 'L';
	if(windowWidth < 1300 ) type='M';
	else if(windowWidth < 600 ) type='S';
	let stateVlu = { screenType:type, windowWidth:windowWidth, windowHeight:windowHeight };
	return stateVlu;
   // this.setState(stateVlu);

  }
  
  render() {
	  
    return (

		
      <div className="application" style={{maxWidth:'2000px',textAlign:'center',display: 'inlineBlock'}}>
	
		<Website device = { this.state.screenType } />
      </div>
    );
  }

  
}

export default App;



