import translator from './translator.json'

var language = 'en';
let text = function ( key ){
	
	return translator[language][key];
}

let translatedText = function ( key, lan  ){
	
	return translator[lan][key];
}

let setLocale=function( locale ){
	
	language = locale;
}

let toggleLanguage = function(){
	if(language == 'en'){
		language ='ka';
		return translator['ka']['language'];
	}
	else{
		language ='en';
		return  translator['en']['language']
	}
}
let locale = function(){
	return language;
}


export{text,setLocale,toggleLanguage,locale,translatedText}


