import React, { Component } from 'react';
import Header from './header.js';
import HomeBody from './homebody.js';
import Faq from './faq.js';
import Footer from './footer.js';
import GetStarted from './get-started.js';
import About from './about.js';
import Contact from './contact.js';
import HomeMobile from './home_m.js';
import {toggleLanguage, locale} from './../util/i18n.js';

export default class Website extends Component{
	
	constructor( props ){
		
		super( props );
		this.state = { menu : 'home', toggle : 0,};
		
	}

	render(){
		
		return (
			<div >
				<div className = 'w3-top' >
					<Header menuChanged = { this.menuChanged.bind( this )} toggleLanguage={this.toggleLanguage.bind(this)} />				
				</div>
				<div ><center>
				{ this.getContent( this.state.menu, this.props.device ) }
				</center>
				</div>
				
				<Footer device={this.props.device}/>	
			</div>
		);
	}
	
	menuChanged = function( menu ){		
	
		this.setState( { menu: menu } );
		
	}
	getStarted = function(){
		this.menuChanged('getstarted');
	}
	
	getContent = function( menu, device ){
		if( device =='L' ) return this.getLargeContent(menu);
		else if( device =='S' ) return this.getMediumContent(menu);
		else if( device =='M' ) return this.getMediumContent(menu);
		
	}
	
	getLargeContent=function( menu){
		if( menu == 'home' ) return (<HomeBody getStarted = { this.getStarted.bind( this )} device={this.props.device} language={locale()}/>);
		else if(  menu == 'faq' ) return ( <Faq device={this.props.device}/> );
		else if(  menu == 'getstarted' ) return ( <GetStarted device={this.props.device}/> );
		else if(  menu == 'about' ) return ( <About device={this.props.device}/> );
		else if(  menu == 'contact' ) return ( <Contact device={this.props.device}/> );
		else return this.getContent( 'home' );
	}
	getMediumContent=function( menu){
		if( menu == 'home' ) return (<HomeMobile getStarted = { this.getStarted.bind( this )} device={this.props.device}/>);
		else if(  menu == 'faq' ) return ( <Faq device={this.props.device}/> );
		else if(  menu == 'getstarted' ) return ( <GetStarted device={this.props.device}/> );
		else if(  menu == 'about' ) return ( <About device={this.props.device}/> );
		else if(  menu == 'contact' ) return ( <Contact device={this.props.device}/> );
		else return this.getContent( 'home' );
	}
	toggleLanguage = function(){	
		
		toggleLanguage();	
		this.forceUpdate();
	}
	
}


