import React, { Component } from 'react';
import 'w3-css/w3.css';
import './website.css'
import logo from './../images/logo.png';
import nse from './../images/nse.png';
import zerodha from './../images/zerodha.svg';
import {openZerodha,openNSE,terms } from './../util/util.js';
import { text,locale } from './../util/i18n.js';

export default class Footer extends Component{

	constructor( props )
   {
       super(props);
   }

	render(){
		let islarge = this.props.device =='L';
		if( islarge )
			return(
				<div className= {islarge?' w3-bottom' : '' }>
					<div className=' w3-row  ' >
						<div className='w3-container w3-row w3-small w3-margin-left w3-left ' style={{maxWidth:'90%'}} >

							Trades on <a href='#' onClick={openNSE} >< img src={nse} style={{height:'12px'}} /></a>
							 &nbsp;Equity Margine Intraday Square @&nbsp;
							<a href='#' onClick={openZerodha} ><img src={zerodha} style={{height:'10px'}}/></a>.
						</div>
						<div className=' w3-small w3-margin-right w3-right ' style={{maxWidth:'90%'}} >
							<i>{text('caution')}</i> {text('tc')[0]}&nbsp;
							<a href ='#' onClick={terms} >{text('tc')[1]}</a>&nbsp;{text('tc')[2]}
						</div>
					</div>
					<div className = ' w3-border w3-border-black w3-container w3-text-white w3-row w3-padding animate__animated animate__slideInUp '	style = { {  backgroundColor:'#2A1B10', height:'70px' } } >

						<div className='w3-col l4 w3-justify' style = { { whiteSpace: 'nowrap' } } >
							<img src={logo} style={{height:'20px'}}/> <i>Prameyaka Technologies</i>
						</div>
						<div className='w3-col l4' >&nbsp;</div>
						<div className = ' w3-col l4' style={{whiteSpace: 'nowrap'}}>
							<i className="fa fa-envelope-o" aria-hidden="true"></i>
								&nbsp;<a  style={{textDecoration: "none" }} href = "mailto:contact@prameyaka.com" >contact@prameyaka.com</a>
									{/*<br/>
								<i className="fa fa-phone" aria-hidden="true"></i>
									&nbsp;91-9538901718*/}

						</div>
					</div>
				</div>

			);
		else {
			return(
				<div className = 'w3-container w3-black w3-text-white'	style={{height:'100px'}}>

					<div className='w3-padding' style={{whiteSpace: 'nowrap'}}>
						<img src={logo} style={{height:'20px'}}/> <i>Prameyaka Technologies</i>
					</div>

					<div  style={{whiteSpace: 'nowrap'}}>
						<i className="fa fa-envelope-o" aria-hidden="true"></i>
							&nbsp;<a  style={{textDecoration: "none" }} href = "mailto:contact@prameyaka.com" >contact@prameyaka.com</a>
								{/*<br/>
							<i className="fa fa-phone" aria-hidden="true"></i>
								&nbsp;91-9538901718*/}

					</div>
				</div>
			);
		}

	}
}



