
import React, { Component } from 'react';
import contact from './../images/15.jpg';
import {sendEmail,d} from './../util/util.js';

export default class Contact extends Component{
	
	constructor( props )
   {
       super(props);
	   this.state = { 
			status:0,
			name:null,
			email:null,
			phone:'',
			message : '',
			error:null,
			data :{
			  
				"sender":{  
					"name":null,
					"email":"message@contact.com",
				},
				"to":[  
					{  
						"email":"contact@prameyaka.com",
						"name":"Prameyaka"
					}
				],
			   "subject":"Message from Contact page.",
			   "htmlContent":"<html><body><p>Name : #name#</p><p>Email : #email#</p><p>Phone : #phone#</p> <p>Message : #message#</p></p></body></html>",
			}	
		};

	}
   
	render(){
		
		
		let width = this.props.device == 'L' ? '40%' : '90%';
		return (
			<div  className='w3-padding-64 ' style={{ fontFamily: 'Helvetica, Arial, sans-serif', fontSize:'17px', textShadow: '1px 1px 1px  #fff',  backgroundImage: "url(" + contact + ")", backgroundSize: 'cover', backgroundOrigin: 'border-box',backgroundPosition: 'top' , }}>
					
				
				<div  id = 'contactpage' className='w3-card w3-margin w3-white w3-animate-zoom' style={{  width:width}}>
					<header class="w3-container w3-border w3-border-bott">
					  <h3>Contact</h3>
					</header>
					
					<div className='w3-border w3-padding w3-left-align'>
						<p className='w3-left-align ' style={{ paddingLeft:'5%',paddingRight:'5%'}}>
						For any queries feel free to contact us. You can also email us to<i> contact@prameyaka.com</i>. We will get back to you soon.
						</p>
						
						<p className='w3-left-align ' style={{	width:'250px', paddingLeft:'5%'}}>
							<label className='w3-opacity'>Your Name  <span className='w3-text-red'>*</span></label>
							<input class="w3-input" type="text" size='30' onChange={this.setName.bind(this)} />
							
						</p>
						<p className='w3-left-align ' style={{ width:'250px', paddingLeft:'5%'}}>  
							<label className='w3-opacity'>Email address <span className='w3-text-red'>*</span></label>
							<input class="w3-input" type="text"  size='30' onChange={this.setEmail.bind(this)} />
							
							
						</p>
						<p className='w3-left-align ' style={{width:'250px',paddingLeft:'5%'}}>
							<label className='w3-opacity'>Phone number</label>
							<input class="w3-input" type="text" size='30' value={this.state.phone}	 onChange={this.setPhone.bind(this)} />
							
						</p>
						
						<p className='w3-left-align  ' style={{paddingLeft:'5%', paddingRight:'5%'}}>
							<label className='w3-opacity'>
							Message <span className='w3-text-red'>* </span> <span className='w3-opacity w3-small'>Max 1500 characters</span> </label>
							<textarea class="w3-input w3-border" type="text" style={{height:'150px'}} 
							
							onChange={this.setMessage.bind(this)} />
							
						</p>
						<div className='w3-text-red w3-center'>{this.state.error}</div>
						<p className='w3-center w3-padding-24 ' >
							<button className='w3-button w3-black' 
							disabled={this.state.status > 0 }
							onClick={this.send.bind(this)}>submit</button>
						</p>
						
					</div>
				</div>
				
				
			</div>);
   }
   
   setName = function(e){
	   let source = e.srcElement || e.target;
	   this.setState({name:source.value,error:null});
   }
   setEmail = function(e){
	   let source = e.srcElement || e.target;
	   this.setState({email:source.value,error:null});
   }
   setPhone = function(e){
	   let source = e.srcElement || e.target;
	   this.setState({phone:source.value,error:null});
   }
   setMessage = function(e){
	   let source = e.srcElement || e.target;
	   this.setState({message:source.value,error:null});
   }
   validateEmail =function (email) {
			const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
			return re.test(String(email).toLowerCase());
	}
	
    send = async function() { 
	  
		if( this.state.name == null || this.state.name.trim().length < 3 ){
			this.setState({error:'Provide your full name.'});
			return;
		}
		else if( this.state.email == null || this.validateEmail(this.state.email) == false ){
			this.setState({error:'Invalid email address.'});
			return;
		}
		else if( this.state.phone != null 
			&& this.state.phone.trim().length >0 
			&& this.state.phone.length < 10 ){
				
			this.setState({error:'Inalid phone number.', phone:''});
			return;
		}
		else if( this.state.message == null 
				|| this.state.message.length < 10 
				|| this.state.message.length > 1500 ){
					
			this.setState({ error:'message size : ' + this.state.message.length + '.  Message length should between 10 to 1500 chracters. ' });
			return;
		}
		else{
			document.getElementById('contactpage').style.cursor = 'auto';
			let data  = this.state.data;		
			data.sender.name = this.state.name.trim();
			
			data.sender.email =d('[186,207,180,236,174,171,170,183,176,179,183,235,174,167,201,95,177,195,212]','MjAyMDEwMDIwMDU1NTg');
			
			let content = data.htmlContent;
			content = content.replace('#name#',this.state.name.trim());
			content = content.replace('#email#',this.state.email.trim());
			content = content.replace('#phone#',this.state.phone);
			content = content.replace('#message#',this.state.message.trim());
			data.htmlContent = content;
			let response = await sendEmail(data);
			if( response != null && response.messageId!= null && response.messageId.length > 10 ){
				document.getElementById('contactpage').style.cursor = 'auto';
				this.setState({error:"Your message has been send.", status:1});
			}
			else{
				document.getElementById('contactpage').style.cursor = 'auto';
				this.setState({error:"Error while submitting your message. Please email the message to contact@prameyaka.com", status:-1});
			}
		}
	}
	
}


