				"ಪ್ರಮೇಯಕದ ಬಳಕೆಯನ್ನು ಸಾಧ್ಯವಾದಷ್ಟು ಸರಳವಾಗಿರುವಂತೆ, ಸುರಕ್ಷತಾ ಮುನ್ನೆಚ್ಚರಿಕೆಯಾಗಿ ಸೆಬಿ ನಿರ್ದೇಶಿಸಿದ ದೈನಿಕ  ಪ್ರವೇಶಿಕವನ್ನು ಹೊರತುಪಡಿಸಿರೆ, ಇತರ ಯಾವುದೇ ಶ್ರಮಪಡುವ ಅಗತ್ಯವಿಲ್ಲದಂತೆ ವಿನ್ಯಾಸ ಮಾಡಲಾಗಿದೆ. ಯಾವುದೇ ಹೊಸ ಸೌಲಭ್ಯವನ್ನು ನಿರ್ಮಿಸುವಾಗ ಅಥವಾ ಸೇರಿಸುವಾಗ ಕೇವಲ ಜಾಲತಾಣಗಳನ್ನು ಬಳಸುವ ಪ್ರಾಥಮಿಕ ತಿಳುವಳಿಕೆಯಷ್ಟೇ  ಇರುವ ಬಳಕೆದಾರರನ್ನು ಗಮನದಲ್ಲಿಟ್ಟಿರುತ್ತೇವೆ. ಅದೇ ಸಮಯದಲ್ಲಿ, ಉತ್ತಮ ಶೇರು ಮಾರುಕಟ್ಟೆ  ಕೌಶಲ್ಯ ಹೊಂದಿರುವವರಿಗೂ  ಉಪಯುಕ್ತವಾಗುವಂತೆ ಪ್ರಮೇಯಕವನ್ನು ನಿರ್ಮಿಸಲಾಗಿದೆ."
