			"started-reviewproduct-12-txt": "ಹಿಂದಿನ ಪ್ರಮಾಣೀಕರಣದ ದಿನಾಂತ್ಯದ ಪ್ರತಿಗಳನ್ನು ನೋಡಲು, ಪ್ರವೇಶ ಪಡೆದು ದಿನಾಂಕದ ಬಳಿಯಿರುವ ತಾರೀಕು ಪಟ್ಟಿ ಚಿಹ್ನೆಯನ್ನು ಕ್ಲಿಕ್ಕಿಸಿ, ದಿನವನ್ನು ಆಯ್ಕೆ ಮಾಡಿ.",
