			"started-reviewproduct-21-txt": "ಪ್ರವೇಶಿಸಿದ ನಂತರ, ಪ್ರಮೇಯಕದ ಹಿಂದಿನ ಕಾರ್ಯಕ್ಷಮತೆಯ ಅಂಕ್ಯಾಂಶಗಳನ್ನು ನೋಡಲು  ಪ್ರಮುಖ ಪರಿವಿಡಿಯ \"P / L Reports\" ನ್ನು ಕ್ಲಿಕ್ಕಿಸಿ.",
			"started-reviewproduct-3-hdr": "ನೇರ ಪ್ರಮಾಣೀಕರಣವನ್ನು ವೀಕ್ಷಿಸಿ ",
			"started-reviewproduct-3-txt": "ಪ್ರಮೇಯಕವನ್ನು ಬಳಸುವ ಮುನ್ನ ಮಾದರಿ ವರ್ತಕನ ವಹಿವಾಟುಗಳ ನೇರ ಪ್ರಮಾಣೀಕರಣವನ್ನು ಕನಿಷ್ಠ ಒಂದೆರಡು ದಿನಗಳಿಗಾದರೂ ವೀಕ್ಷಿಸುವುದು ಉತ್ತಮ. ಪ್ರಸ್ತುತವಾಗಿ ಜಿರೋಧವನ್ನು ಮಾದರಿ ವರ್ತಕನ ಮಧ್ಯವರ್ತಿಯಾಗಿ ಸಂಯೋಜಿಸಲಾದ್ದು, ಪ್ರತಿದಿನ ಶೇರು ಮಾರುಕಟ್ಟೆ ಯ ಆರಂಭದ ಮೊದಲು ಹಾಜರಾಗುವನು.  ",
			"started-opps-1-hdr": "ಚಿಲ್ಲರೆ  ವ್ಯಾಪಾರಿಗಳ ಯಂತ್ರ-ಚಾಲಿತ ಶೇರು ವಹಿವಾಟಿಗೆ ಪೂರ್ಣ ಮಾನ್ಯತೆಯಿಲ್ಲ.",
			"started-opps-1-txt": "ಭಾರತೀಯ ಶೇರು ವಿನಿಮಯ ಕೇಂದ್ರಗಳು ಸಂಪೂರ್ಣ ಯಂತ್ರ-ಚಾಲಿತ ಶೇರು ವಹಿವಾಟನ್ನು ಅಂಗೀಕರಿಸುವಲ್ಲಿ ಬಹಳ ಜಾಗರೂಕವಾಗಿವೆ. ಯಂತ್ರಚಾಲಿತ ವಹಿವಾಟಿನ ಸಾಧಕ-ಬಾಧಕಗಳನ್ನು ಪರಿಶೀಲಿಸಿ ನಿಧಾನವಾಗಿ ಯಂತ್ರಚಾಲಿತ ಶೇರು ವಹಿವಾಟಿಗೆ ತೆರೆದುಕೊಳ್ಳುತ್ತಿದ್ದಾರೆ. ಆದರೆ ವಿನಿಮಯವು ವೈಯಕ್ತಿಕ, ಏಕ ಬಳಕೆದಾರರಿಗೆ ಮತ್ತು ಅನುಮೋದಿತ ತಾಂತ್ರಿಕ ಕ್ರಮಾವಳಿಗಳಿದ್ದರೆ ಯಂತ್ರಚಾಲಿತ ಶೇರು ವ್ಯವಹಾರವನ್ನು ಅನುಮತಿಸುತ್ತದೆ.",
			"started-opps-2-hdr": "ಸ್ವಲ್ಪ ಕಾಯೋಣ... ",
			"started-opps-x-txt": "ನಮ್ಮ ವ್ಯಾಪಾರ ಪ್ರಕ್ರಿಯೆಗಳು ಮಾನವ ಮಧ್ಯಸ್ಥಿಕೆಗಳನ್ನು ಒಳಗೊಂಡಿದ್ದರೂ, ವ್ಯಾಪಾರ ವಿನಂತಿಗಳು ಯಂತ್ರಚಾಲಿತವಾಗಿ ತಯಾರಾಗುತ್ತವೆ. ನೀವು ನಮಗೆ ಅನುಮತಿಯಿತ್ತರೆ, ನಿಮ್ಮ ಪರವಾಗಿ ಪ್ರಮೇಯಕಾ ರಚಿಸಿದ ವ್ಯಾಪಾರ ವಿನಂತಿಗಳನ್ನು ಪರಿಶೀಲಿಸಿ ಕಳುಹಿಸುತ್ತೇವೆ.",
			"started-opps-x-hdr": "ಪರಿಶೀಲನೆಯನ್ನು  ಪತಿನಿಧಿಸಿಸಿ ",
