import React, { Component } from 'react';
import 'w3-css/w3.css';
import hf from './../images/hf.png';
import logo from './../images/logo_m.png';
import logo_ka from './../images/prameyaka_ka_t.png';
import 'font-awesome/css/font-awesome.min.css';
import { text,locale,translatedText } from './../util/i18n.js';

const styles ={
		img :{
			 height: '22px',
		},
		img_ka :{
			 height: '23px',
		},


		btnClass : 'w3-button w3-round w3-small w3-white w3-padding-small	',
	};



export default class Header extends Component{

	constructor( props ){

		super( props );
		//alert(toggleLanguage());

	}

	render(){
		let lng = locale();
		return(
			<div className='w3-top '>
				<div className="w3-bar w3-border-bottom  w3-border-brown w3-white" >
					<div className="w3-bar-item w3-left" style={{ alignContent:'left', maxWidth:'40%'}}>

						<button className='w3-button w3-round w3-padding-small w3-hover-white w3-ripple'
							onClick = {this.home.bind(this)}>
							<img  style={lng == 'en' ? styles.img : styles.img_ka}
							src={ lng == 'en' ? logo : logo_ka} alt="logo"  />
						</button>
					</div>

					<div className='w3-bar-item w3-hover-text-blue w3-text-red w3-hide-small' >
						<a href='#' onClick={this.props.toggleLanguage} style={{textDecoration: "none" }}>
						{text('language')}</a></div>
					<div className='w3-bar-item  w3-margin-right  w3-right'>

						<div className="w3-dropdown-hover w3-cell w3-margin-right" >
							<button className={styles.btnClass} type="button" id="dropdownMenuButton" >
								<i className="fa fa-bars fa-lg" aria-hidden="true"></i>
							</button>
							<div className="w3-dropdown-content w3-bar-block  " id ='menumar' >
								<div className= {'w3-border-left w3-border-black ' } >
									<button
										className="w3-button w3-left-align w3-block w3-small  w3-white "
										onClick={this.about.bind(this)}
										>{text("about-menuitem")}
									</button>

									<button
										className="w3-button w3-left-align w3-block w3-small w3-white w3-border w3-border-light-gray"
										onClick={this.getStarted.bind(this)}
										>{text("button-getstarted")}
									</button>
									<button
										className="w3-button w3-left-align w3-block w3-small w3-white w3-border w3-border-light-gray"
										onClick={this.faq.bind(this)}
										>{text("faqs")}
									</button>
									<button
										className="w3-button w3-left-align w3-block w3-small w3-white w3-border w3-border-light-gray"
										onClick={this.contact.bind(this)}
										>{text("contact")}
									</button>
									<button
										className="w3-button w3-left-align w3-block w3-small w3-white w3-border w3-border-light-gray w3-hide-large w3-hide-medium"
										onClick={this.props.toggleLanguage} style={{textDecoration: "none" }}>
									{ translatedText('language', lng)  }
									</button>
								</div>
							</div>

						</div>
						<div className='w3-cell w3-margin-left'>
							<button className="w3-button  w3-block w3-white w3-padding-small"
							onClick={this.more}>{text('login')}
							</button>
						</div>

					</div>

				</div>
			</div>
		);
	}

	faq = function(){
		this.props.menuChanged( 'faq' );
	}
	home = function(){
		this.props.menuChanged( 'home' );
	}
	about = function(){
		this.props.menuChanged( 'about' );
	}
	getStarted = function(){
		this.props.menuChanged( 'getstarted' );
	}
	contact = function(){
		this.props.menuChanged( 'contact' );
	}
	more = function(){
		window.location ="https://more.prameyaka.com";
		//more.prameyaka.com
	}


	menuChanged = function( event ){

		let source = event.target || event.source;

		this.props.menuChanged( source.id );
	}
}

function w3_open() {
  if (mySidebar.style.display === 'block') {
    mySidebar.style.display = 'none';
  } else {
    mySidebar.style.display = 'block';
  }
}
var mySidebar = document.getElementById("mySidebar");

function w3_open() {
  if (mySidebar.style.display === 'block') {
    mySidebar.style.display = 'none';
  } else {
    mySidebar.style.display = 'block';
  }
}

// Close the sidebar with the close button
function w3_close() {
    mySidebar.style.display = "none";
}



