import React, { Component } from 'react';
import logo_bw from './../images/logo_bw.png';
import Description from './description.js';
import { text,locale } from './../util/i18n.js';
const styles ={
	
	featureCard :{
				
				textShadow: '1px 1px 1px #fff',
				fontFamily:'sans-serif',
				color:'#000',
				fontSize:'15px',			
				//float: 'left',
				lineHeight: 1.8,		
				textAlign: 'left',
				
				
			},
			
			cardDiv : 'w3-round-xxlarge ',
			
		
			
			vertiStyle :{
				letterSpacing:'1.3px',
				fontSize:'14px',
			},
			subtext:{
				fontSize:'14px',
				opacity:0.7,
				marginLeft:'25px',
				marginTop: '-1em',
			}
	
	}

const cards = [];
const lbtns = [];
const items = ['overview',
		'ultrasimple', 
		'livedemo',
		'onemonthfree',
		'payasyougain',
		'roboexpertbacking',
		'backtested',
		'hybrid',
		'cloudbased',
		'tunable',
		'saftywithtransparency',
		'modeltrader'  ];

		
	

let pageno = 0;
let language = null;

export default class Features extends Component{
	
	constructor( props )
   {
       super( props );
	   this.state = { cards : 12,pagenm : 0};
	   this.loadCards();
   }
   
  
   
   render(){
	   if( language != locale() ){
		   // alert(this.state.language);
			this.loadCards();
			language = locale();
		   //	this.setState({language:locale()});
	   }
	  
	   return(
			
			<div className="w3-cell-row w3-border-bottom ">
				<div className='w3-cell w3-hide-small w3-hide-medium animate__animated animate__slideInLeft animate__delay-1s' style = { { width : '15%' } } >	
				
					{ 
						Object.entries( this.btns ).map( ( [ key, value ] ) =>{								
							
							return this.lbtn( key, value, this.state.pagenm );
							}
						)
					}					
				
				</div>
				
				<div className='w3-cell w3-display-container animate__animated animate__fadeIn ' style={{paddingLeft:'5%', paddingRight:'5%',}}   >
					
					<div className ="w3-container w3-cell-bottom w3-border-top w3-border-gray " />					
					{ this.cardList( this.state.pagenm, this.props.nmCards ) }					
								
				<div className ="w3-container w3-cell-bottom w3-large w3-padding " />	
				<div className='w3-display-bottommiddle w3-border-top w3-border-gray' style={{fontSize:'12px', width:'85%'}}>	
					
					<button className = "w3-button  w3-left " onClick = { this.prev.bind( this ) }>
						<i className="fa fa-arrow-circle-left fa-2x" aria-hidden="true"></i>
					</button>			
					
					<button className = "w3-button w3-right" onClick = { this.next.bind( this ) } >
						 <i className="fa fa-arrow-circle-right fa-2x" aria-hidden="true"></i>
					</button>	
			
				</div>
				</div>
				
				<div className='w3-cell w3-hide-small w3-hide-medium animate__animated animate__slideInRight animate__delay-1s' style={{width:'15%'}}>
				
					{ 
						Object.entries(this.btns).map(([key,value])=>{
								return this.rbtn( key, value, this.state.pagenm );
							}
						)
					}
				
				</div>
			</div>  
	   );
   }
	   
	   componentDidMount(){
		  // this.loadButtons();
		  
	   }
	btns = [];  
   
   lbtn = function( idx, lable, pagenm ){
	   
	   if( idx > 5 ) return ;
	   let clz = ' w3-button w3-block    w3-left-align ' ;  
	   
	   
	   if( idx == 0  ) {
		   clz = ' w3-border-top  ' + clz;
	   }
	   if( idx == pagenm  ) {
		   clz = ' w3-border-black w3-border-bottom w3-border-top w3-text-black ' + clz;
	   }
	   else clz = clz + ' w3-border-bottom w3-text-gray';
	  
		return (
		   <div key={'ftr' +idx }
				className={clz} 			
				style={styles.vertiStyle} 
				//onClick={() => { this.jmp(idx).bind(this)} }>{lable}
				onClick={() => { this.setState( { pagenm:parseInt( idx ) } )} }>{lable}
			</div> );
	 
	   
	  
   }
   
   rbtn = function( idx, lable, pagenm ){
	   
	   if( idx < 6 ) return ;
	   let clz = ' 	w3-button w3-block     w3-right-align';
	  
	   
	   
	  if( idx == 6  ) {
		   clz = ' w3-border-top ' + clz;
	   }
	   if( idx == pagenm  ) {
		   clz = ' w3-border-black w3-border-bottom w3-border-top w3-text-black' + clz;
	   }
	   else clz = clz + ' w3-border-bottom  w3-text-gray';
	  
		return (
			<div 
				key={'b'+idx}
				className={clz} 			
				style={styles.vertiStyle} 
				//onClick={() => { this.jmp(idx).bind(this)} }>{lable}
				onClick={() => { this.setState( { pagenm:parseInt( idx ) } )} }>{lable}
				
			</div> );   
	  
		}


   
   cardList = function( pagenm, nmCards ){
	   
	   let items = [];
	
	   for (let index = pagenm ; index < (pagenm +nmCards) ; index++ ) {
		    
			items.push( cards [index]);
		}
		
		return (
			<div className=' w3-center w3-round-large w3-margin-bottom' 
			style={{display: 'flex', justifyContent:'space-around', }} >
			{items}
				
			</div>
		);
	   
   }
   next = function(){
		
		let vlu = this.state.pagenm+this.props.nmCards;
		if( vlu >= this.state.cards )vlu = 0;
	
		this.setState({pagenm:vlu});
	}
	prev = function(){
		
		let vlu = this.state.pagenm-this.props.nmCards;
		if( vlu < 0 )vlu = 0;
		this.setState({pagenm:vlu});
	}
	
	

	loadCards = function(){
	
		this.btns = text('featuremenulabels');

		cards =[];
		let item = null;
		for( var ctr =0; ctr < items.length;ctr++){
			item = items[ctr];
			cards.push(
					
					<div className={styles.cardDiv} style={styles.featureCard} key='c1'>	
						
						<p  style={{fontSize:'16px',  color:'#000'}}>
							{/*	<b>{this.btns[ctr]}</b> :&nbsp; */}
							{text(item)}
						</p>	
			</div>
		
			);
		}
	
	}
  
}


