
import React, { Component } from 'react';
import filler1 from './../images/filler.jpg';
import filler2 from './../images/filler2.jpg';
import filler3 from './../images/filler3.jpg';
import filler4 from './../images/filler4.jpg';
import filler5 from './../images/filler5.jpg';
import filler6 from './../images/filler7.jpg';

import { text,locale } from './../util/i18n.js';
import nse from './../images/nse.png';
import zerodha from './../images/zerodha.svg';


const cards = [];
let language = null;
let styles ={
	card:{
		class : 'w3-round-xlarge w3-border ',
		style:{
			width:'46%',

		},

		img :{
			class:'w3-round-xlarge w3-grayscale',
			style:{height:'130px'},

		},
		imgM :{
			class:'w3-round-large w3-grayscale-max',
			style:{height:'20px'},

		},
		desc:{
			class:'w3-cell w3-left-align ',
			style:{verticalAlign:'middle',lineHeight: 2,},
		},
	},
};

const imgs= [filler1,filler2,filler3,filler4,filler5,filler6];
/*
const quotes= [
				'"I can buy the banges today..."',
				'"aagaya, hamara scooter repair ka paisa..."',
				'"Wow, what a finish..."',
				'"Even I can trade in the stock market..."',
				'"I don\'t need part time job, I can study more..."',
				'"Huh, should have tuned exit schenarios..."',
				'"What is todays\' pocket money ?"',



			];
const descs =[
			'Enjoy small breaks from your regular work by watching your investment anywhere at any time.',
			'Now, you just need to have basic internet surfing knowledge to earn from stock market.	',
			'Intraday trading is interesting, fascinating, exciting even more than a cricket or football match.',
			'Ready-made platform tailered to provide an ultra simple trading experiance for those who have no knowledge, skill or exposure to stock market.',
			'No need of extra effort or extra time to earn extra money. ',
			'Exit scenarios can be tuned for limiting the loss or booking the profit to avoid unwanted surprises.',
			];*/
export default class Marketing extends Component{

	constructor( props )
   {
       super(props);

		this.state ={

			nmCards:this.props.device =='L' ? 2 : 1,
			pageno:0,
			retrier:null,
			show:true,
			tmpageno:0,

		};

		if(this.props.device =='S' ){
			//styles.card.width = '90%';
			this.loadCardsS('90%') ;
		}
		else if(this.props.device =='M' ){

			//styles.card.style.width = '90%';
			this.loadCardsS('70%') ;
		}
		else{

			this.loadCards();
		}


   }
   componentDidMount() {

	   this.state.retrier = setInterval( this.slide.bind( this ), 10000 );

   }
   componentWillUnmount(){
		clearInterval( this.state.retrier );
	}



	render(){
		 if(language != locale() ){
		   // alert(this.state.language);
			if(this.props.device =='S' ){
			//styles.card.width = '90%';
			this.loadCardsS('90%') ;
		}
		else if(this.props.device =='M' ){

			//styles.card.style.width = '90%';
			this.loadCardsS('80%') ;
		}
		else{

			this.loadCards();
		}
		  language =locale();
	   }
		 let clz = this.state.show ? 'animate__animated animate__fadeIn animate__delay-2s1' : 'animate__animated animate__fadeOut animate__delay-2s'
		return(
			<div id='marketingCards' className={this.state.show ? 'w3-show' : 'w3-hide' }>
				<div  className={'animate__animated animate__fadeIn animate__delay-2s1 '} style={{verticalAlign:'middle', paddingTop:'60px', paddingBottom:'60px',width:'100%'}}>
					<div >
						<div className='w3-cell ' style={{ width:'2%'}} >
							<button className = "w3-button w3-small w3-padding-small w3-text-dark-gray" onClick = { this.prev.bind( this ) }>
									❮❮
							</button>
						</div>
						<div className='w3-cell ' style={{ width:'90%'}} >
							{this.cardList(this.state.pageno, this.state.nmCards)}
						</div>
						<div className='w3-cell ' style={{ width:'2%'}} >
							<button className = "w3-button w3-small w3-padding-small w3-text-dark-gray" onClick = { this.next.bind( this ) }>
								 ❯❯
							</button>
						</div>
					</div>

					<div className='w3-padding-16 w3-row w3-hide' >
						<div className='w3-col s1 w3-padding-small'>
							<button className = "w3-button w3-small w3-padding-small w3-text-dark-gray" onClick = { this.prev.bind( this ) }>
								<i className="fa fa-arrow-circle-left fa-2x" aria-hidden="true"></i>
							</button>
						</div>
						<div className='w3-col s10 ' >


						<i>&nbsp;</i></div>

						<div className='w3-col s1'>
							<button className = "w3-button w3-small w3-padding-small w3-text-dark-gray" onClick = { this.next.bind( this ) }>
								<i className="fa fa-arrow-circle-right fa-2x" aria-hidden="true"></i>
							</button>
						</div>

					</div>
				</div>

			</div>
		);
	}
	slide = async function(){
			let pageno = this.state.pageno;
			this.setState( { pageno : -1, show:false  } );
			await new Promise(r => setTimeout(r, 10));

			pageno = pageno + this.state.nmCards;
			if ( pageno >= imgs.length ) pageno = 0;

			this.setState( { pageno : pageno, show:true  } );
	}

	cardList = function( pagenm, nmCards ){

		if(pagenm < 0 )
			return (
				<div className='w3-round-large w3-left-align'
					style={{width:'100%', display: 'flex', justifyContent:'space-around', }} >

					&nbsp;
				</div>
			);

	   let items = [];
	   let ctr = pagenm;

	   for (let index = 0 ; index < nmCards ; index++ ) {

		   if( ctr >= imgs.length ) ctr = 0;

		   items.push( cards [ctr]);
				ctr = ctr+1;
		}

		return (


			<div className='w3-round-large w3-left-align'
				style={{width:'100%', display: 'flex', justifyContent:'space-around', }} >

				{items}

			</div>
		);

   }

   prev=function(){

	  window.clearInterval( this.state.retrier );
	   let pageno = this.state.pageno;
		pageno = pageno - this.state.nmCards;
		if ( pageno < 0 ) pageno = 0;

		this.setState( { pageno : pageno  } );
   }
    next=function(){

		window.clearInterval( this.state.retrier );
		let pageno = this.state.pageno;
		pageno = pageno + this.state.nmCards;
		if ( pageno >= imgs.length ) pageno = imgs.length - this.state.nmCards;

		this.setState( { pageno : pageno  } );
   }

	loadCards = function(){

		cards =[];
		let quotes =text('marketing_q');
		let descs = text('marketing');
		for( let no = 0; no < imgs.length; no++ ){

			cards.push(

				<div className={styles.card.class} style={styles.card.style } key={'crd' +no }>
					<div className='w3-cell  w3-left-align' >
						<img src={imgs[no]} style={ styles.card.img.style}
						className={styles.card.img.class}/>
					</div>
					<div className='w3-cell w3-left-align ' style={styles.card.desc.style}>
						<h6 className='w3-margin-left'>
							<b><i>"{quotes[no]}"</i></b><br/>
								{descs[no]}
						</h6>
					</div>
				</div>

			);
		}
	}

	loadCardsS = function(width){

		cards =[];
		let quotes =text('marketing_q');
		let descs = text('marketing');

		for( let no = 0; no < imgs.length; no++ ){

			cards.push(

				<div className='w3-card ' style={{maxWidth:'90%'}} >
					<div >
						<img src={imgs[no]}  className=' w3-grayscale-max'
						style={{maxWidth:'100%'}}	/>
					</div><br/>
					<div className='w3-left-align ' >
						<h6 className='w3-margin-left w3-margin-right' key={'crd' +no }>
							<b><i>{quotes[no]}</i></b><br/>
								{descs[no]}
						</h6>
					</div>
				</div>

			);
		}
	}
}



