import React, { Component } from 'react';
import {openNew,terms} from './../util/util.js';
import logo_bw from './../images/logo_bw.png';
//import background from './../images/bg.png';
import background from './../images/14.jpg';
import { text,locale } from './../util/i18n.js';

const styles ={

	featureCard :{

				fontFamily: 'Helvetica, Arial, sans-serif',
				fontSize:'17px',
				textShadow: '2px 2px 2px  #fff',


				//fontFamily:'sans-serif',
				//color:'#555',
				//fontSize:'15px',
				float: 'left',
				lineHeight: 1.8,
				textAlign: 'left',
				paddingBottom:'50px'
			},

			cardDiv : 'w3-padding',

			selectedStyle :{
				letterSpacing:'1.1px',
				fontSize:'14px',
				fontWeight: 'bold',
			},

			vertiStyle :{
				letterSpacing:'1.2px',
				fontSize:'14px',
			},


	}

//const cards = [];

//const btns = ['WELCOME','KNOW FACTS','REVIEW PRODUCT','OOPS...','TRAIL','UNDERSTAND PRICING', 'BE SATISFIED'];

//const btns = [text('started-welcome'),text('started-knowfacts'),text('started-reviewproduct'),text('started-opps'),text('started-trial'),text('started-understandpricing'),text('started-besatisfied') ];





let pageno = 0;
let language = null;

export default class GetStarted extends Component{

	constructor( props )
   {
       super(props);
	   this.state ={ pagenm : 0 };

   }
    btns = [];
	cards = [];
   render(){
	   if( language != locale() ){
		   // alert(this.state.language);
			this.loadCards();
			language = locale();
		 //  this.setState({language:locale()});
	   }
	   let padding = this.props.device == 'L' ? '50px' : '1px';

		return(

			<div className="w3-cell-row" style={{maxWidth: '100%',  paddingTop:'40px', paddingLeft:padding, paddingRight:padding,backgroundImage: "url(" + background + ")", backgroundSize: 'cover', backgroundOrigin: 'border-box', backgroundPosition: 'bottom', imageWidth: '30px 48px', paddingLeft:'5%', paddingRight:'5%',}} >

				<div>
					<header className="w3-container w3-center">
					  <h3 style={{ textTransform: 'uppercase', padding:'20px'}} >{text('started-hdr')}</h3>
					</header>

				<div className='w3-cell w3-hide-small w3-hide-medium w3-border-left  w3-border-brown animate__animated animate__slideInLeft animate__animated animate__slideInLeft' style = { { width : '15%' } } >

				{
					Object.entries( this.btns ).map( ( [ key, value ] ) =>{

							return this.lbtn( key, value, this.state.pagenm );
						}
					)
				}


				</div>

				<div className='w3-cell w3-display-container '	 >

					<div className ="w3-container w3-cell-bottom w3-hide-small w3-hide-medium" />
					{ this.cardList( this.state.pagenm ) }

					<div>&nbsp;</div>
					<div className='w3-display-bottommiddle w3-border-top w3-border-blue-gray' style={{fontSize:'14px', width:'90%'}}>

						<button className = "w3-button  w3-left " onClick = { this.prev.bind( this ) }>
							❮❮ PREV
						</button>

						<button className = "w3-button w3-right" onClick = { this.next.bind( this ) } >
							 NEXT ❯❯
						</button>

					</div>
				</div>
				<div className=' w3-hide-small w3-center' style = { { height:'100px', opacity:'0.5' } } >
					&nbsp;
				</div>
			</div>

		</div>

	   );
   }

	   componentDidMount(){

		   this.loadCards();
		   this.setState({cardnm:0});
	   }


   lbtn = function( idx, lable, pagenm ){

		if( idx > this.btns.length-1 ) return ;
		let clz = 'w3-button w3-block    w3-left-align' ;
		   let style = styles.vertiStyle;

		if( idx == 0  ) {
			clz = ' w3-border-top ' + clz;
		}
		if( idx == pagenm  ) {
			clz = ' w3-border-black w3-border-bottom w3-border-top ' + clz;
			style = styles.selectedStyle;
		}
		else clz = clz + ' w3-border-bottom';

		return (

			<div key ={'id'+idx }
				className={clz}
				style={style}
				onClick={() => { this.setState( { pagenm:parseInt( idx ) } ) } }>{lable}
			</div> );
   }


   cardList = function( pagenm ){


		return (
			<div className=' w3-center w3-round-large w3-left-align '  style={{paddingLeft:'5%', minHeight:'600px'}} >
			{this.cards[pagenm]}

			</div>
		);

   }
   next = function(){

		let vlu = this.state.pagenm+1;
		if( vlu >= this.btns.length ) return;//vlu = btns.length-1;

		this.setState({pagenm:vlu});
	}
	prev = function(){

		let vlu = this.state.pagenm-1;
		if( vlu < 0 )vlu = 0;
		this.setState({pagenm:vlu});
	}
	jmp = function( idx ){

		this.setState( { pagenm:parseInt( idx ) } );
	}


	loadCards = function(){

		this.btns = [text('started-menu-welcome'),text('started-menu-knowfacts'),text('started-menu-reviewproduct'),text('started-menu-opps'),text('started-menu-trial'),text('started-menu-understandpricing'),text('started-menu-besatisfied') ];
		this.cards = [];
		this.cards.push(

			<div className={styles.cardDiv} style={styles.featureCard}>
				<p >
					<b><li>{text('started-welcome-hdr')}</li></b>
					{text('started-welcome-txt-1')}


				</p>
				<p >

					{text('started-welcome-txt-2')} <br/><br/> {text('started-welcome-txt-3')}

				</p>
			</div>);

		this.cards.push(

			<div className={styles.cardDiv} style={styles.featureCard}>
				<p  >
					<b><li>{text('started-knowfacts-1-hdr')}</li></b>

					{text('started-knowfacts-1-txt')}

				</p>
				<p  >
					<b><i> - {text('started-knowfacts-11-hdr')} : </i></b>
					{text('started-knowfacts-11-txt')}


				</p>
					<p>
						<b><i> - {text('started-knowfacts-12-hdr')} : </i></b>
						{text('started-knowfacts-12-txt')}
					</p>
				<p>
					<b><li>{text('started-knowfacts-2-hdr')}</li></b>
					{text('started-knowfacts-2-txt')}
				</p>



				<p >
					<b><li>{text('started-knowfacts-3-hdr')}</li></b>
					* {text('started-knowfacts-31-txt')}<br/>
					* {text('started-knowfacts-32-txt')[0]}
						<a href='#' onClick={terms}> {text('started-knowfacts-32-txt')[1]}</a>
						{text('started-knowfacts-32-txt')[2]}

				</p>
			</div>

			);

		this.cards.push(
				<div className={styles.cardDiv} style={styles.featureCard}>


					<p >
						<b><li>{text('started-reviewproduct-1-hdr')}</li></b>
						{text('started-reviewproduct-1-txt')}

					</p>
					<div>
						<ul>
						<li>

							<i> {text('started-reviewproduct-11-txt')[0]}<a href='#' onClick={this.more}>{text('started-reviewproduct-11-txt')[1]}</a> {text('started-reviewproduct-11-txt')[2]}<b>{text('started-reviewproduct-11-txt')[3]}</b> {text('started-reviewproduct-11-txt')[4]}
							</i>
						</li>
						<li>
							<i>{text('started-reviewproduct-12-txt')}</i>
						</li>

						</ul>
					</div>
					<div >
						<b><li>{text('started-reviewproduct-2-hdr')}</li></b>
						{text('started-reviewproduct-2-txt')}


						<ul><li><i>{text('started-reviewproduct-21-txt')} </i></li></ul>
					</div>

					<p >
						<b><li>{text('started-reviewproduct-3-hdr')}</li></b>
						{text('started-reviewproduct-3-txt')}
					</p>
				</div>
			);
			this.cards.push(
				<div className={styles.cardDiv} style={styles.featureCard}>


					<p>
						<b><li>{text('started-opps-1-hdr')}</li></b>
							{text('started-opps-1-txt')}
					</p>

					<p >
						<b><li>{text('started-opps-2-hdr')}</li></b>
						{text('started-opps-2-txt')}<br/><br/>
						{text('started-opps-3-txt')}

							{/*<br/><span className="w3-opacity w3-small" >* {text('started-opps-2-ftr')}</span><br/>*/}
					</p>

				</div>
			);



			this.cards.push(

				<div className={styles.cardDiv} style={styles.featureCard}>
					<p >
						<b><li>{text('started-trial-1-hdr')}</li></b>
						{text('started-trial-1-txt')[0]}
						<a href='#' onClick={this.zerodha}><b>{text('started-trial-1-txt')[1]}</b></a>
						{text('started-trial-1-txt')[2]}
					</p>
					<p >
					<b><li>{text('started-trial-2-hdr')}</li></b>
					{text('started-trial-2-txt')}</p>
					<p ><i>
						 * {text('started-trial-21-txt')[0]} <a href='#' onClick={this.more}>{text('started-trial-21-txt')[1]}</a> {text('started-trial-21-txt')[2]}</i>
					</p>
					<p>
						{text('started-trial-22-txt')}

					</p>

					<p >
						<b><li>{text('started-trial-23-hdr')}</li></b>
						{text('started-trial-23-txt')}
					</p>
				</div>
			);


			this.cards.push(
				<div className={styles.cardDiv} style={styles.featureCard}>

					<p >
					<b><li>{text('started-payasyougain-1-hdr')}</li></b>
						{text('started-payasyougain-11-txt')}
						<br/><br/>
						{text('started-payasyougain-12-txt')}

						<br/><span className="w3-opacity w3-small" >* {text('started-payasyougain-1-ftr')}  </span><br/>
					</p>
					<p>
					<b><li>{text('started-payasyougain-2-hdr')}</li></b>
						{text('started-payasyougain-2-txt')}
					</p>
				</div>
			);

			this.cards.push(
				<div className={styles.cardDiv} style={styles.featureCard}>

					<p >
				<b><li>{text('started-besatisfied-1-hdr')}</li></b>
				{text('started-besatisfied-1-txt')}</p>

			<p >
				<b><li>{text('started-besatisfied-2-hdr')}</li></b>
				{text('started-besatisfied-2-txt')}
			</p>
				</div>
			);


	}
   zerodha = function(){
	   openNew('https://zerodha.com');
   }
   more = function(){

	   window.open("https://more.prameyaka.com","_self")

   }
}



